import Layout from "../../components/Layout"
import IconButton from "../../components/IconButton"
import {IoMdAdd} from "@react-icons/all-files/io/IoMdAdd"
import { useEffect, useReducer } from "react"
import Form from "./Form"
import { useDispatch, useSelector } from "react-redux"
import { get, post } from "../../application/middlewares"
import BgLoader from "../../components/BgLoader"
import Table from "../../components/Table/Table"
import Head from "../../components/Table/Head"
import Row from "../../components/Table/Row"
import Cell from "../../components/Table/Cell"
import Body from "../../components/Table/Body"
import toast from "react-hot-toast"
import {BsImage} from "@react-icons/all-files/bs/BsImage"
import {BiTrash} from "@react-icons/all-files/bi/BiTrash";
import {BiEdit} from "@react-icons/all-files/bi/BiEdit"

function reducer (state, action){
    console.log(action)
    switch (action.type){
        case "ADD_FORM":
            return {
                ...state,
                visible:true,
                form:"add",
                values:{}, 
                title:"Форма добавления"
            }
        case "SET_CLOSE":
            return{
                ...state,
                visible:false
            }
        case "SET_DATA":
            return {
                ...state,
                loading:false,
                data:action.payload.news,
                count:action.payload.count
            }
        case "SET_LOADING":
            return {
                ...state, 
                loading:action.payload
            }
        case "ADD_NEWS":
            return {
                ...state,
                data:[action.payload, ...state.data],
                count:state.count+1
            }
        case "SET_UPLOAD_IMAGE":
            return {
                ...state,
                data:state.data.map(item => {
                    if(item.id === action.payload.id){
                        item.destination = action.payload.destination
                    }return item
                })
            }
        case "UPDATE_FORM":
            return{
                ...state,
                form: "update",
                title:"Форма обновления", 
                visible:true,
                values:action.payload
            }
            case "DELETE_NEWS":
                return {
                    ...state,
                    data:state.data.filter(item => item.id !== +action.payload),
                    loading:false
                }
        default: return state
    }
}

function News (){
    const [state, setState] = useReducer(reducer, {
        data:[], form:"", loading:true, visible:false
    })

    const dispatch = useDispatch()
    const token = useSelector(state => state.auth.token)

    useEffect(()=>{
        // console.log(JSON.stringify([1]))
        dispatch(get({
            url:`api/admin/get-news`,
            token,
            action: (response) =>{
                if(response.success){
                    setState({type:"SET_DATA", payload:response.data.rows})
                }else{
                    toast.error("Неизвестная ошибка")
                    setState({type:"SET_LOADING", payload:false})
                }
            }
        }))
        
    }, []) // eslint-disable-line react-hooks/exhaustive-deps

    const deleteNews = (id) =>{
        // console.log("Hello mothe fuker")
        setState({type:"SET_LOADING", payload:true})
        dispatch(post({
            url:`api/admin/delete-news/${id}`,
            token,
            action: (response) =>{
                if(response.success){
                    setState({type:"DELETE_NEWS", payload:id})
                    // setState({type:"SET_LOADING", payload:false})
                }else{
                    setState({type:"SET_LOADING", payload:false})
                    toast.error("something went wrong")
                }
            }
        }))
    }

    return (
        <Layout header = {<Header handleClick = {()=>setState({type:"ADD_FORM", payload:""})}/>}>
            <Form
                visible={state.visible}
                form = {state.form}
                title = {state.title}
                token = {token}
                setCloseModal = {()=>setState({type:"SET_CLOSE", payload:""})}
                addNews = {(value)=>setState({type:"ADD_NEWS", payload:value})}
                setUploadedImage = {(value) => setState({type:"SET_UPLOAD_IMAGE", payload:value})}
            />
            <BgLoader loading = {state.loading}/>
            <div className="w-full h-full px-6 overflow-y-auto pb-20 ">
                <Table>
                    <Head>
                        <Row>
                            <>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    ID
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Заголовок на туркменском
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                Заголовок на русском
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                Заголовок на англицском
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Создано
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Картинка
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Действие
                                </div>
                            </Cell>
                            </>
                        </Row>
                    </Head>
                    <Body>
                        {
                            state.data?.length > 0 ?
                            state.data.map(item => (
                                <Row key = {item.id}>
                                    <>
                                    <Cell>
                                        <div className = "p-2">
                                            {item.id}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className = "p-2">
                                            {item.title_tm}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className = "p-2">
                                            {item.title_ru}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className = "p-2">
                                            {item.title_en}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className = "p-2">
                                            {item.created_at}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className="p-2 flex justify-center items-center">
                                            {item?.destination ? <img className="object-contain rounded-md w-28 h-28" src={`${process.env.REACT_APP_FILE_URL}/${item?.destination}-big.webp`} alt="CTYPES" />: <BsImage className="text-4xl text-gray-500 opacity-80"/>}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className = "p-2 flex flex-row">
                                            {/* <IconButton tooltip = {"Изменить"} handleClick = {()=>setState({type:"UPDATE_FORM", payload:item})} icon = {<BiEdit className = "text-2xl"/>}/> */}
                                            <IconButton tooltip = {"Удалить"} handleClick = {()=>deleteNews(item.id)} icon = {<BiTrash className = "text-2xl"/>}/>
                                        </div>
                                    </Cell>
                                    </>
                                </Row>
                            ))
                            :
                            <>
                            </>
                        }
                    </Body>
                </Table>
            </div>
        </Layout>
    )
}

const Header = ({handleClick}) =>(
    <div className="flex flex-row justify-between items-center">
        <IconButton tooltip = "Добавить новость" handleClick={handleClick} icon={<IoMdAdd className="text-2xl "/>}/>
        {/* <div className="w-80">
            <SearchInput action={(value) => handleSearch(value)} placeholder="Поиск"/>
        </div> */}
    </div>
)

export default News